
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video,
input {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  outline: none;
  text-decoration: none;
}
*,
*:after,
*:before {
  box-sizing: border-box;
}
html {
  font-family: "PT Sans", sans-serif;
  font-size: 14px;
  color: #475965;
}
body {
  background: #f0f3f6;
}
.app {
  display: flex;
  min-height: 100vh;
  flex-flow: column;
}
#container {
  flex-grow: 1;
}
a,
a:active {
  color: #475965;
  outline: none;
  text-decoration: none;
}
i,
.icon {
  color: #475965;
  font-size: 1.1rem;
  transition: color 0.15s;
  margin-left: 5px;
  text-decoration: none;
}
i:hover {
  color: #53c4b8;
}
#container {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
}
.flex-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.flex-col {
  display: flex;
  flex-flow: column;
  justify-content: center;
}
section {
  width: 400px;
  box-shadow: 1px 0 20px rgba(0, 0, 0, 0.05);
}
.section-head {
  background: #53c4b8;
  color: white;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  padding: 10px;
}
.section-head h2 {
  font-size: 1.5rem;
  line-height: 1.5rem;
}
.section-body {
  background: white;
  padding: 15px 10px;
}
.toolbar {
  height: 25px;
  margin-bottom: 15px;
}
footer {
  background-color: #f9f9f9;
  border-top: 1px solid rgba(0, 0, 0, 0.05);
  text-align: center;
  padding: 20px 30px;
  margin-top: 30px;
}

header {
  background: #53c4b8;
  width: 100%;
  height: 80px;
  padding: 0 60px;
  margin-bottom: 40px;
}
.lang {
  background: blue;
  background-size: cover;
  width: 32px;
  height: 21px;
}
.lang-0 {
  background-position-y: 0;
}
.lang-1 {
  background-position-y: 63px;
}
.lang-2 {
  background-position-y: 42px;
}
.lang-3 {
  background-position-y: 21px;
}

.jobs,
input {
  font-family: "PT Sans", sans-serif;
  height: 25px;
  border: 1px solid rgb(223, 226, 230);
  border-radius: 5px;
  font-size: 0.95rem;
  padding-left: 10px;
}
.jobs-options a:focus,
input:focus,
.jobs-select {
  border: 2px solid #53c4b8;
  padding-left: 9px;
}
.jobs,
.lvl {
  width: 49%;
}
.jobs {
  position: relative;
}
input[type="number"] {
  width: 48%;
  text-align: center;
}
input[type="text"] {
  width: 100%;
  margin: 15px 0;
}
.jobs-options {
  position: absolute;
  width: 100%;
  background: white;
  border: 2px solid #53c4b8;
  top: 21px;
  left: 0;
}
.unclick {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  background: transparent;
  z-index: 0;
}
.options,
.options:focus,
.options:hover {
  z-index: 9999;
  height: 25px;
  padding-left: 6px;
}
.options:focus,
.options:hover {
  background: #53c4b8;
  color: white;
  padding-left: 6px;
}

.card {
  background: #f0f3f6;
  margin-top: 5px;
  border-radius: 5px;
  padding: 3px 10px 3px 5px;
}
.card-recipe {
  background: #bbebde;
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 0px;
}
.card-recipe i:hover {
  color: rgb(0, 0, 0);
}
.item-img {
  background-size: cover;
  width: 35px;
  height: 35px;
}
.info {
  margin-left: 8px;
}
.primary {
  justify-content: flex-start;
  line-height: 1.5rem;
}
.primary img {
  width: 0.9rem;
  margin-right: 5px;
}
.secondary {
  font-size: 0.9rem;
}
.right {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.right input {
  margin-right: 5px;
  margin-left: 10px;
  width: 55px;
  height: 30px;
}
.right input:focus,
.right a:focus {
  border: none;
  outline: none;
}
.card-little {
  border-left: #bbebde 2px solid;
  padding-left: 7px;
  padding-top: 5px;
  margin-top: -5px;
}
.card-little .card {
  padding: 0px 0;
  padding-right: 10px;
}
.card-little .item-img {
  width: 27px;
  height: 27px;
}
.card-little,
.card-little .secondary {
  font-size: 0.85rem;
  line-height: 1rem;
}
.card-little img {
  width: 0.8rem;
}


































